import axios from 'axios';
import { API_URL } from '../utils/consts.js';

class FiltersService {
  static filterQuery = '';

  // –º–µ—Ç–æ–¥ —â–æ –ø–æ–≤–µ—Ä—Ç–∞—î –∑–Ω–∞—á–µ–Ω–Ω—è –æ–±—Ä–∞–Ω–æ—ó –∫–∞—Ç–µ–≥–æ—Ä—ñ—ó –ø—ñ—Å–ª—è –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—è –Ω–∞ –∫–Ω–æ–ø–∫—É
    getFilterQuery() {
        if (FiltersService.filterQuery === '') {
          return 'Muscles';
      } return FiltersService.filterQuery;
  }
  // –º–µ—Ç–æ–¥ —â–æ –∑–±–µ—Ä—ñ–≥–∞—î –∑–Ω–∞—á–µ–Ω–Ω—è –æ–±—Ä–∞–Ω–æ–≥–æ —Ñ—ñ–ª—å—Ç—Ä—É  –ø—ñ—Å–ª—è –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—è –Ω–∞ –∫–Ω–æ–ø–∫—É
  setFilterQuery(value) {
    FiltersService.filterQuery = value;
  }
  // –º–µ—Ç–æ–¥ —Ä–æ–±–∏—Ç—å –∑–∞–ø–∏—Ç –≤—ñ–¥–ø–æ–≤—ñ–¥–Ω–æ –¥–æ –æ–±—Ä–∞–Ω–æ–≥–æ —Ñ—ñ–ª—å—Ç—Ä—É —ñ
  // –ø–æ–≤–µ—Ä—Ç–∞—î –≤—ñ–¥—Ñ—ñ–ª—å—Ç—Ä–æ–≤–∞–Ω—ñ –¥–∞–Ω—ñ –¥–ª—è –ø–æ–¥–∞–ª—å—à–æ–≥–æ –≤–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è
  async fetchFilteredData(filter, limit, page) {
    const axiosOptions = {
      filter,
      limit,
      page,
    };
    // const filteredData = await axios.get(`${API_URL}/filters`, axiosOptions
    // );
    const filteredData = await axios.get(`${API_URL}/filters`, {
      params: axiosOptions,
    });
    return filteredData.data;
  }
}

export { FiltersService };
