function renderPagination(
  paginationWrapper,
  currentPage,
  totalPages,
  clickHandler
) {
  paginationWrapper.innerHTML = ''; // –û—á–∏—Å—Ç–∏—Ç–∏ –ø–æ–ø–µ—Ä–µ–¥–Ω—ñ–π –≤–º—ñ—Å—Ç

  const maxPrev = 2;
  const maxNext = 2;

  // –°—Ç–≤–æ—Ä—é—î –∫–Ω–æ–ø–∫—É
  function createPageButton(page) {
    const btn = document.createElement('button');
    btn.classList.add('page-number');
    btn.textContent = page;
    if (page === currentPage) {
      btn.disabled = true;
      btn.classList.add('active');
    }
    btn.addEventListener('click', clickHandler);
    return btn;
  }

  // –î—ñ–∞–ø–∞–∑–æ–Ω —Å—Ç–æ—Ä—ñ–Ω–æ–∫
  const startPage = Math.max(1, currentPage - maxPrev);
  const endPage = Math.min(totalPages, currentPage + maxNext);

  // –ö–Ω–æ–ø–∫–∞ "‚Üê"
  // if (currentPage > 1) {
  //   const prev = document.createElement('button');
  //   prev.textContent = '‚Üê';
  //   prev.addEventListener('click', () => {
  //     renderPagination(currentPage - 1, totalPages);
  //   });
  //   pagination.appendChild(prev);
  // }

  // –ö–Ω–æ–ø–∫–∏ —Å—Ç–æ—Ä—ñ–Ω–æ–∫
  for (let i = startPage; i <= endPage; i++) {
    paginationWrapper.appendChild(createPageButton(i));
  }
  // –ö–Ω–æ–ø–∫–∞ "‚Üí"
  // if (currentPage < totalPages) {
  //   const next = document.createElement('button');
  //   next.textContent = '‚Üí';
  //   next.addEventListener('click', () => {
  //     renderPagination(currentPage + 1, totalPages);
  //   });
  //   pagination.appendChild(next);
  // }
}

export { renderPagination };
